// src/app/page.tsx
import ClientMapPage from '@/components/ClientMapPage';
import { supabase } from '@/lib/supabaseClient';

// 全体の最新投稿を取得
async function getAllRecentPosts() {
    const { data } = await supabase
        .from('hazard_posts')
        .select('id, reason, address, created_at')
        .order('created_at', { ascending: false })
        .limit(10);
    return data || [];
}

export default async function Home() {
    const recentPosts = await getAllRecentPosts();

    // トップページの場合は cityData なしで recentPosts だけ渡す
    return <ClientMapPage recentPosts={recentPosts} />;
}
